body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.background{
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  background-attachment: fixed;
}

a{
  text-decoration: none;
  color: wheat;
}

.home-button{
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home{
  position: absolute;
  top: 50%;
  left: 40%;
  background-color: wheat;
  padding: 5px 10px;
  font-size: 30px;
  border-radius: 8px;
  border: 1px solid wheat;
  color: black;
}

.home:hover{
  color:#4CAF50
}

.home-button:hover{
  color: #4CAF50;
}

.header{
  display: flex;
  justify-content: center;
  padding: 20px 0;
  gap: 30px;
  font-weight: bold;
  width: 100vw;
  font-size: 30px;
  
}

.icon{
  max-width: 35px;
}

.active{
  color: #4CAF50;
}

.cards{
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid gray;
  padding: 5px;
  min-width: 250px;
  background-color: wheat;
}

.store-img{
  padding-bottom: 5px;
}

.card-bottom{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.card-text{
  padding-left: 5px;
}

.quantity-change{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.quantity-button{
  background-color: white;
  border: 1px solid black;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 16px;
  padding: 0 6px
}

.quantity{
  min-width: 32px;
  text-align: center;
  border: 1px solid black;
  border-left: none;
  border-right: none;
  background-color: white;
}

.add-to-cart {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}

.cart-icon{
  color: orange;
  font-size: 30px;
  font-weight: bold;
  display: flex;
}

.cart-img{
  width: 33%;
  height: auto;
}

.cart-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 2px solid gray;
  padding: 5px;
  max-width: 400px;
  background-color: wheat;
  margin: 0 5px;
}

.cart-cards{
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.delete{
  border: none;
  background-color: white;
  padding: 1px 0;
  text-align: center;
  text-decoration: underline;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  margin-top: 10px;
  background-color: wheat;
}

.delete:hover{
  color: red;
}

.cart{
  display: flex;
  justify-content: center;
  gap: 100px;
  flex-wrap: wrap;
}

.subtotal, .shipping, .total{
  display: flex;
  justify-content: space-between;
}

.shipping{
  border-bottom: 1px solid black;
  margin-bottom: 10px;
}

.total-heading{
  margin-bottom: 15px;
}

.checkout{
  min-width: 400px;
  max-width: 425px;
  border: 2px solid gray;
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  background-color: wheat;
  margin: 0 5px;
}

.place-order{
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  margin-top: 15px;
}